import React, { Component } from 'react';
import logo from '../logo.svg';

class Artist extends Component {
    constructor(props) {
        super(props);
        this.state = {
            result: this.props,
            show: false
        }
    }

    showModal = (e) => {
        e.preventDefault();
        this.setState({ show: true });
    }

    hideModal = () => {
        this.setState({ show: false });
    }

    componentDidMount = () => {
        const {id} = this.props.match.params;
        let myHeaders = new Headers();
        myHeaders.append('Content-Type', 'application/json; charset=utf-8');

        
        fetch(`https://api.discogs.com/artists/${id}`, myHeaders)
        .then(res => res.json())
        .then(data => {
            console.log(data);
            this.setState({
                profile: data.profile,
                realname: data.realname
            }, () => console.log(this.state))
        })
    }

    render() {
        let artistContent;
        const { id } = this.props.match.params;
        const { profile } = this.state;
        const { realname } = this.state;
        const { result } = this.props.location.state;
        artistContent = (
             <React.Fragment>                     
                <div className="container artist">
                        <div className="artist-top">
                            <div className="artist-top-left">
                                <div className="artist-image">
                                    <a href={`/artist/${id}`} onClick={this.showModal.bind(this)}>
                                        <img src={result.cover_image} alt={result.title} />  
                                    </a>  
                                </div>
                                <div className="artist-image-link">
                                    <a href={`/artist/${id}`} onClick={this.showModal.bind(this)}>More Images</a>
                                </div>
                            </div>
                            <div className="artist-top-center-artist-desc">
                                <div className="artist-title">
                                <p>{result.title}</p>
                                </div>
                                 <div className="artist-real-name">
                                    <p>Real Name: {realname}</p>
                                </div>
                                <div className="artist-profile">
                                    <p>Profile: {profile}</p>
                                </div>
                                <div className="artist-sites">
                                    <p>Sites: {profile}</p>
                                </div>
                                <div className="artist-aliases">
                                    <p>Aliases: {profile}</p>
                                </div>
                                <div className="artist-variations">
                                    <p>Variations: {profile}</p>
                                </div>
                                <div className="read-more">
                                    <p><a href="#!">Read More..</a></p>
                                </div>
                            </div>
                        </div>
                        
                </div>
            </React.Fragment>
        )
        return (
            <React.Fragment>
                <div className="logo logo-search">
                    <img src={logo} alt="logo" />
                </div> 
                {artistContent}
            </React.Fragment>
        )
    }
}
export default Artist;