import React, { Component } from 'react';
import Index from './components/Index';
import Search from './components/Search';
import Release from './components/Release';
import Artist from './components/Artist';
import Label from './components/Label';
import NoMatch from './components/NoMatch';
import './App.css';
import './index.css';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';

class App extends Component {
  render() {
    return (
      <Router>
        <Switch>
          <Route exact path="/sp/react/react-discogs-search" component={Index} /> 
          <Route exact path="/sp/react/react-discogs-search/search" component={Search} /> 
          <Route exact path="/sp/react/react-discogs-search/release/:id" component={Release} />
          <Route exact path="/sp/react/react-discogs-search/artist/:id" component={Artist} />
          <Route exact path="/sp/react/react-discogs-search/label/:id" component={Label} />
          <Route component={NoMatch} />
        </Switch>
      </Router>
    );
  }
}

export default App;
