import React, { Component } from 'react';
import SearchDropdown from './SearchDropdown';
import Spinner from './Spinner';
import SearchButton from './SearchButton';
import { debounce } from 'lodash';

class SearchBar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            prefetch: <SearchButton />,
            search_term: '',
            results: []
        }
    }

    handlePreFetch = () => {
        this.setState({
            prefetch: <Spinner />
        });
    }

    // Use debounce technique to handle API requests    
    handleSearch = debounce(() => {

        let search_term = document.querySelector('.search-term').value;

        Promise.all([

        ])

        fetch(`https://api.discogs.com/database/search?q=${search_term}&token=${process.env.REACT_APP_API_KEY}`)
        .then(res => res.json())
        .then(data => {
            this.setState({
                search_term: search_term,
                results: data.results,
                prefetch: <SearchButton />,
            }, () => console.log(this.state));
        }).catch(error => {console.error(error)});         
    }, 400);

    render() {
        const { prefetch } = this.state;
        const { results } = this.state;
        const { search_term } = this.state;
        
        return(
            <React.Fragment>
                <div className="search-bar">
                    <form action="/Search">
                        <input type="text" className="search-bar-input search-term" placeholder="Search Discogs Database" onKeyUp={this.handlePreFetch.bind(this)} onChange={this.handleSearch.bind(this)} />
                        <div className="search-status">
                            {prefetch}
                        </div>
                    </form>
                </div>
                <SearchDropdown 
                    search_term={search_term}
                    results={results}
                />
            </React.Fragment>
        )
    }
}
export default SearchBar;