import React, {Component} from 'react';
import { Link } from 'react-router-dom';

class SearchDropdown extends Component {
    constructor(props) {
        super(props);
        this.state = {
            start_index: 0,
            visible: 10,
            search_term: this.props,
            results: this.props
        }
    }
    
    handleResultTitleLink = () => {
        console.log('Link clicked');
    }

    handleClick = (e) => {
        e.preventDefault();
        const elem = document.querySelectorAll('.cat-btn');
        [].forEach.call(elem, (el) => {
          el.classList.remove('active');
        });
        e.target.parentElement.classList.add('active');
       
        switch(e.target.text) {
            case 'Everything': 
            this.setState({
                type: 'all' 
            });
              break;
            case 'Releases':
            this.setState({
                type: 'release' 
            });
              break;
            case 'Artists':
            this.setState({
                type: 'artist' 
            });
              break;
            case 'Labels':
            this.setState({
                type: 'label' 
            });
              break;
            default: 
        }
    }

    render() {
        let releasesContent;
        // let inCollectionCount = 0;
        // let inWantlistCount = 0;
        const { search_term } = this.props;
        const { results } = this.props;
        const { start_index, visible } = this.state;
        const { type } = this.state;


        if(results === undefined || results.length === 0 || search_term === '') {
             return(
                 <React.Fragment>
                    
                 </React.Fragment>
             );
        }

        releasesContent = (
            <div className="search-dropdown">
                <div className="categories">
                    <div className="everything cat-btn active">
                        <a href="/" onClick={this.handleClick}>Everything</a>
                    </div>
                    <div className="releases cat-btn">
                        <a href="/" onClick={this.handleClick}>Releases</a>
                    </div>
                    <div className="artists cat-btn">
                        <a href="/" onClick={this.handleClick}>Artists</a>
                    </div>
                    <div className="labels cat-btn">
                        <a href="/" onClick={this.handleClick}>Labels</a>
                    </div>
                </div>
                <div className="search-dropdown-items">
                {type === 'all' ? results.slice(start_index, visible).map((result, index) => (
                    //When you click on 'Everything tab'
                    <Link key={index} className="dropdown-results-link" to={{
                        pathname: `/sp/react/react-discogs-search/${result.type}/`+result.id, state:{result}
                    }}>
                    <div className="result-item result-item-1">
                        <div className="result-image">
                            <img src={result.thumb === '' ? 'https://s.discogs.com/images/default-label.png' : result.thumb} alt="result" />
                        </div>
                        <div className="result-title">
                        <p>{result.title}<span className="format"> { result.type==='artist' ? '' : '' } { result.type==='master' ? '(all versions)' : '' } </span></p>
                            <p><span className="format">{result.type==='master' ? result.title : result.type}</span></p>
                        </div>
                        <div className="result-status">
                            {/* {result.user_data.in_collection === true ? <img className="collection-icon" src={require('../in-collection-icon.svg')} alt="collection icon" /> : <img className="collection-icon" src={require('../in-wantlist-icon.svg')} alt="collection icon" />} */}
                            {result.user_data.in_collection === true ? <div className="collection-icon collection">{result.user_data.in_collection}</div> : <div className="collection-icon wantlist">{result.user_data.in_wantlist}</div>}
                        </div>
                    </div>
                    </Link>
                )) : ''}
                {type === 'release' ? results.slice(start_index, visible).filter((result, index) => result.type==='release').map((result, index) => (
                    <Link key={index} className="release dropdown-results-link" to={{
                        pathname: `/sp/react/react-discogs-search/${result.type}/`+result.id, state:{result}
                    }}>
                    <div className="result-item result-item-1">
                        <div className="result-image">
                            <img src={result.thumb === '' ? 'https://s.discogs.com/images/default-label.png' : result.thumb} alt="result" />
                        </div>
                        <div className="result-title">
                            <p>{result.title} <span className="format">{result.format[1] !== '' ? result.format[1] : ''}{result.format[2] ? ',' : ''} {result.format[2] !== '' ? result.format[2] : ''}</span></p>
                            <p><span className="format">{result.type}</span></p>
                        </div>
                        <div className="result-status">
                            {/* {result.user_data.in_collection === true ? <img className="collection-icon" src={require('../in-collection-icon.svg')} alt="collection icon" /> : <img className="collection-icon" src={require('../in-wantlist-icon.svg')} alt="collection icon" />} */}
                            {result.user_data.in_collection === true ? <div className="collection-icon collection">{result.user_data.in_collection}</div> : <div className="collection-icon wantlist">{result.user_data.in_wantlist}</div>}
                        </div>
                    </div>
                    </Link>
                )) : ''}
                {type === 'artist' ? results.slice(start_index, 4).filter((result, index) => result.type==='artist').map((result, index) => (
                    <Link key={index} className="artist dropdown-results-link" to={{
                        pathname: `/sp/react/react-discogs-search/${result.type}/`+result.id, state:{result}
                    }}>
                    <div className="result-item result-item-1">
                        <div className="result-image">
                            <img src={result.thumb === '' ? 'https://s.discogs.com/images/default-label.png' : result.thumb} alt="result" />
                        </div>
                        <div className="result-title">
                            <p>{result.title} <span className="format">{result.format}</span></p>
                            <p><span className="format">{result.type}</span></p>
                        </div>
                        <div className="result-status">
                            {/* {result.user_data.in_collection === true ? <img className="collection-icon" src={require('../in-collection-icon.svg')} alt="collection icon" /> : <img className="collection-icon" src={require('../in-wantlist-icon.svg')} alt="collection icon" />} */}
                            {result.user_data.in_collection === true ? <div className="collection-icon collection">{result.user_data.in_collection}</div> : <div className="collection-icon wantlist">{result.user_data.in_wantlist}</div>}
                        </div>
                    </div>
                    </Link>
                )) : ''}
                 {type === 'label' ? results.slice(start_index, 4).filter((result, index) => result.type==='label').map((result, index) => (
                    <Link key={index} className="dropdown-results-link" to={{
                        pathname: `/sp/react/react-discogs-search/${result.type}/`+result.id, state:{result}
                    }}>
                    <div className="result-item result-item-1">
                        <div className="result-image">
                            <img src={result.thumb === '' ? 'https://s.discogs.com/images/default-label.png' : result.thumb} alt="result" />
                        </div>
                        <div className="result-title">
                            <p>{result.title} <span className="format">{result.format}</span></p>
                            <p><span className="format">{result.type}</span></p>
                        </div>
                        <div className="result-status">
                            {/* {result.user_data.in_collection === true ? <img className="collection-icon" src={require('../in-collection-icon.svg')} alt="collection icon" /> : <img className="collection-icon" src={require('../in-wantlist-icon.svg')} alt="collection icon" />} */}
                            {result.user_data.in_collection === true ? <div className="collection-icon collection">{result.user_data.in_collection}</div> : <div className="collection-icon wantlist">{result.user_data.in_wantlist}</div>}
                        </div>
                    </div>
                    </Link>
                )) : ''}
                {results && type === undefined ? results.slice(start_index, visible).map((result, index) => (
                    // Initial Dropdown
                    <Link key={index} className="dropdown-results-link" to={{
                        pathname: `/sp/react/react-discogs-search/${result.type}/`+result.id, state:{result}
                    }}>
                    <div className="result-item result-item-1">
                        <div className="result-image">
                            <img src={result.thumb === '' ? 'https://s.discogs.com/images/default-label.png' : result.thumb} alt="result" />
                        </div>
                        <div className="result-title">
                            <p>{result.title} <span className="format"> { result.type==='artist' ? '' : '' } { result.type==='master' ? '(all versions)' : '' } </span></p>
                            <p><span className="format">{result.type==='master' ? result.title : result.type}</span></p>
                        </div>
                        <div className="result-status">
                            {/* {result.user_data.in_collection === true ? <img className="collection-icon" src={require('../in-collection-icon.svg')} alt="collection icon" /> : <img className="collection-icon" src={require('../in-wantlist-icon.svg')} alt="collection icon" />} */}
                            {result.user_data.in_collection === true ? <div className="collection-icon collection">{result.user_data.in_collection}</div> : <div className="collection-icon wantlist">{result.user_data.in_wantlist}</div>}
                        </div>
                    </div>
                    </Link>
                )) : ''}                 
                </div>
            </div>
        );
        
        

        return (
            <React.Fragment>
                {releasesContent}
            </React.Fragment>
        )
    }
}
export default SearchDropdown;