import React, { Component } from 'react';
import logo from '../logo.svg';

class Label extends Component {
    constructor(props) {
        super(props);
        this.state = {
            result: this.props,
            show: false,
            sublabels: [],
            sites: []
        }
    }

    showModal = (e) => {
        e.preventDefault();
        this.setState({ show: true });
    }

    hideModal = () => {
        this.setState({ show: false });
    }

    componentDidMount = () => {
        const {id} = this.props.match.params;
        let myHeaders = new Headers();
        myHeaders.append('Content-Type', 'application/json; charset=utf-8');

        
        fetch(`https://api.discogs.com/labels/${id}`, myHeaders)
        .then(res => res.json())
        .then(data => {
            console.log(data);
            this.setState({
                profile: data.profile,
                sublabels: data.sublabels,
                sites: data.urls
            }, () => console.log(this.state))
        })
    }

    render() {
        let labelContent;
        const { id } = this.props.match.params;
        const { profile } = this.state;
        const { sublabels } = this.state;
        const { sites } = this.state;
        const { result } = this.props.location.state;
        labelContent = (
             <React.Fragment>                     
                <div className="container label">
                        <div className="label-top">
                            <div className="label-top-left">
                                <div className="label-image">
                                    <a href={`/labels/${id}`} onClick={this.showModal.bind(this)}>
                                        <img src={result.type==='label' ? 'https://s.discogs.com/images/default-label.png' : ''} />
                                    </a>  
                                </div>
                                <div className="label-image-link">
                                    <a href={`/labels/${id}`} onClick={this.showModal.bind(this)}>More Images</a>
                                </div>
                            </div>
                            <div className="label-top-center-label-desc">
                                <div className="label-title">
                                    <p>{result.title}</p>
                                </div>
                                <div className="label-profile">
                                    {profile}
                                </div>
                                <div className="label-sublabels">
                                    <p>{sublabels.map(sublabel => (
                                        sublabel.name
                                    )).join(', ')}</p>
                                </div>
                                <div className="label-sites">
                                    <p>{sites.map(site => (
                                        site
                                    )).join(', ')}</p>
                                </div>
                        </div>
                    </div>                        
                </div>
            </React.Fragment>
        )
        return (
            <React.Fragment>
                <div className="logo logo-search">
                    <img src={logo} alt="logo" />
                </div> 
                {labelContent}
            </React.Fragment>
        )
    }
}
export default Label;